.welcome {
    width: 100%;
    height: 100vh;
    background: url('img/bgwelcome.jpg') center/cover no-repeat;
    display: flex;
    justify-content: center;
    animation: welcome 7s ease-in-out infinite;
    text-align: center;
}

.welcome__title {
    margin: 100px 0 0 0;
    text-transform: uppercase;
}

.welcome__title > span {
    color: var(--color-red);
}

@keyframes welcome {
    0% {
        filter: invert(0);
    }
    50%{
        filter: invert(100%)
    }
    100% {
        filter: invert(0);
    }
}
