.favorites__wrapper {
    color: var(--color-white);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    padding: 70px 0 70px 0;
}
.favorites__wrapper > h2 {
    text-transform: uppercase;
}
.favorites__wrapper > hr {
    width: 500px;
}

.no__results {
    color: var(--color-white);
    font-style: italic;
    background-color: var(--color-dark);
    padding: 5px 20px;
    margin-top: 10px;
    letter-spacing: 1.5px;
    opacity: .85;
}

@media (max-width: 599px) {
    .favorites__wrapper > hr {
        width: 90vw;
    }
}
