.character__list{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    margin: 10px 0 0 0;
    padding: 0;
}
.character__item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 20px;
    height: 320px;
    width: 250px;
    background-color: var(--color-white);
    border-radius: 5px;
    box-shadow: var(--box-shadow);
    position: relative;
}

.character__name{
    text-align: center;
    text-transform: uppercase;
    padding: 20px 10px 10px;
    margin: 0;
    font-size: 16px;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.character__img{
    width: 100%;
    max-height: 250px;
    border-radius: 0 0 5px 5px;
    object-fit: cover;
    object-position: center;
    transition: .3s ease-in-out;
}
.character__img:hover {
    filter: brightness(130%);
}
.no__results {
    color: var(--color-white);
    font-style: italic;
    background-color: var(--color-dark);
    padding: 5px 20px;
    margin-top: 60px;
}

.character__favorite {
    position: absolute;
    top: -20px;
    right: 5px;
    width: 35px;
    cursor: pointer;
    filter: drop-shadow(var(--box-shadow));
}
