.header {
    width: 100%;
    height: 50px;
    background-color: var(--color-red);
    box-shadow: var(--box-shadow);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
}

.list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
}
.list__item {
    list-style: none;
}
.list__item > a:hover {
    filter: blur(1px);
}
.list__item > a {
    text-decoration: none;
    text-transform: uppercase;
    margin: 0 10px;
    color: var(--color-white);
}

.list__item:first-child > a {
    margin: 0 50px 0 0;
    color: var(--color-dark);
    font-weight: bold;
}

.active {
    color: var(--color-white) !important;
}

.active__links {
    opacity: .5;
}

.character__favorite {
    width: 15px;
    margin-left: 5px;
}

@media (max-width: 599px) {
    .list__item > a:hover {
        filter: blur(0);
    }
    .list__item > a {
        font-size: 14px;
    }
    .list__item:first-child > a {
        margin: 0 10px 0 0;
    }
}