.error__container {
    margin: 100px auto;
    width: 600px;
    text-align: center;
}

.error__title {
    color: var(--color-white);
}

.error__gif {
    width: 500px;
    height: 281px;
    background: url('img/errorGif.gif') center/cover no-repeat;
    margin: 0 auto;
    opacity: .9;
    transition: opacity .33s ease-in-out;
}

.error__gif:hover {
    opacity: .6;
}