.footer {
    width: 100%;
    height: 50px;
    background-color: var(--color-dark);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
}

.footer > span {
    color: var(--color-white);
    text-transform: uppercase;
    font-size: 14px;
}