.comics__item{
    width: 500px;
    margin: 0 50px;
    position: relative;
}
.comics__name{
    color: var(--color-white);
    text-transform: uppercase;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding: 0 30px;
    margin-top: 0;
    width: 100%;
    font-size: 20px;
}
.comics__img{
    width: 500px;
    height: 600px;
    object-fit: cover;
    object-position: center;
}

.comics__favorite {
    position: absolute;
    top: 35px;
    right: 5px;
    width: 50px;
    cursor: pointer;
    filter: drop-shadow(var(--box-shadow));
}

@media (max-width: 599px) {
    .comics__item {
        margin: 0 50px;
        width: 320px;
    }
    .comics__name {
        padding: 0 15px;
        font-size: 16px;
    }
    .comics__img {
        width: 320px;
        height: 420px;
    }

    .comics__favorite {
        top: 30px;
        right: 15px;
        width: 45px;
    }
}


@media (max-width: 416px) {
    .comics__item {
      margin: 0 50px;
      width: 320px;
    }
    .comics__name {
        padding: 0 15px;
        font-size: 16px;
    }
    .comics__img {
        width: 320px;
        height: 420px;
    }

    .comics__favorite {
        top: 30px;
        right: 15px;
        width: 45px;
    }
}
