.comics__container{
    margin: 0 auto;
    display: flex;
    width: 100%;
    max-width: 600px;
    overflow-x: hidden;
}
.comics__track {
    display: flex;
    transition: all .4s ease-in-out;
}

.no__results {
    color: var(--color-white);
    font-style: italic;
    background-color: var(--color-dark);
    padding: 5px 20px;
    margin-top: 60px;
}

@media (max-width: 599px) {
    .comics__container {
        max-width: 72vw;
    }
}

@media (max-width: 545px) {
    .comics__container {
        max-width: 80vw;
    }
}

@media (max-width: 499px) {
    .comics__container {
        max-width: 83vw;
    }
}

@media (max-width: 470px) {
    .comics__container {
        max-width: 88vw;
    }
}

@media (max-width: 451px) {
    .comics__container {
        max-width: 96vw;
    }
}

@media (max-width: 416px) {
    .comics__container {
        max-width: 600px;
    }
}

